
/* ==========================================================================
   #Content bottom styles - begin
   ========================================================================== */

.edgt-content-bottom {
    
    .edgt-content-bottom-inner{
        box-sizing: border-box;
        
        &.edgt-grid{
            padding: 20px 0;
        }
        
        &.edgt-full-width{
            padding: 20px 50px;
        }
    }
}
/* ==========================================================================
   #Content bottom styles - end
   ========================================================================== */

/* ==========================================================================
   Footer style - begin
   ========================================================================== */

.edgt-page-footer {
    @include edgtRelativeHolderLayout();
    z-index: 100;
    box-sizing: border-box;

    .edgt-footer-top-holder {
        @include edgtRelativeHolderLayout();
        background-color: #222222;
        box-sizing: border-box;

        &.light {
            .widget {
                color :rgba(#fff, .7);
                a {
                    color : #fff;
                    &:hover {
                        color: #a9a9a9;
                    }
                }
                .edgt-course-list-holder.edgt-cl-minimal article .edgt-cli-text .edgt-instructor-name {
                    color :rgba(#fff, .7);
                }
            }
        }

        &.dark {
            background-color: #fafafa;
            .widget {
                color :#888;
                a {
                    color: #313131;
                    &:hover {
                        color: $first-main-color;
                    }
                }
                .edgt-widget-title-holder .edgt-widget-title {
                    color: #313131;
                }

                &.edgt-blog-list-widget{

                    .edgt-post-info-author{
                        .edgt-post-info-author-text,
                        .edgt-post-info-author-link{
                            color: #888!important;
                        }
                    }
                }

            }
        }

        &.edgt-transparent-footer-top {
            background-color: transparent!important;
        }

        .edgt-footer-top-alignment-center{
            text-align: center;
        }
	    
        .edgt-footer-top-alignment-left{
            text-align: left;
        }
	    
        .edgt-footer-top-alignment-right{
            text-align: right;
        }

        .edgt-footer-top-inner{
            box-sizing: border-box;
	        
            &.edgt-grid{
                padding: 98px 0 39px;
            }
	        
            &.edgt-full-width{
                padding: 98px 50px 39px;
            }
        }
    }

    .edgt-footer-bottom-holder {
        @include edgtRelativeHolderLayout();
        background-color: #222222;
        box-sizing: border-box;
        text-align: center;
        font-weight: 600;
        letter-spacing: 0.5px;

        &.light {
            .edgt-footer-bottom-inner {
                border-top-color: #404040;
            }

            a {
                &:not(.edgt-social-icon-widget-holder) {
                    color: rgba(#fff, .4) !important;
                }
            }

            .widget {
                color: #888;
                a {
                    color: rgba(#fff, .4)!important;
                }
            }
        }

        &.dark {
            background-color: #fafafa;
            .edgt-footer-bottom-inner {
                border-top-color: #ebebeb;
            }
            .widget {
                color :#555;
                a {
                    color: #313131;
                }
            }
        }

        &.edgt-transparent-footer-bottom {
            background-color: transparent!important;
        }

        .edgt-footer-bottom-inner {
            box-sizing: border-box;
            border-top: 1px solid rgba(72,72,72,.8);
	        
            &.edgt-grid {
                padding: 20px 0;
            }
	        
            &.edgt-full-width {
                padding: 20px 50px;
            }
        }
    }

    .edgt-boxed &{
        .edgt-footer-top-holder{
            .edgt-footer-top-inner{
                &.edgt-grid{
                    padding: 98px 15px 39px;
                }
            }
        }
        .edgt-footer-bottom-holder {
            .edgt-footer-bottom-inner{
                &.edgt-grid{
                    padding: 20px 0;
                }
            }
        }
    }
}
/* ==========================================================================
   Footer style - end
   ========================================================================== */